import React,{useState,useEffect} from 'react'
import {Card,Table,Button,Modal} from 'antd'
import ButtonGroup from 'antd/lib/button/button-group';
import Confirm from './Confirm'
import axios from 'axios'
import {getTime} from '../utils/time'

const basicUrl='https://www.prebeauty.work'

const mapFiledToChinese={
    id:'序号',
    name:'管理员名',
    tel:'电话',
    birth:'生日',
    position:'职位',
    address:'地址' ,
    time:'入职时间'
}

const Admin = (props) => {
  // console.log(props)
    const [data, setData] = useState([])
    const [columns, setColumns] = useState([])
    const [total, setTotal] = useState(0)
    const [id,setId]=useState('')

    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
      setIsModalVisible(true);
    };
  
    const handleOk = (id) => {
      fetch(basicUrl+'/admin/'+id,{
        method:'delete',
      }).then(res=>res.json())
      .then(res=>{
        console.log(res)
      })
      setIsModalVisible(false);

      let arr=data.filter(item=>item.key!==id)
      // console.log(arr)
      setData(arr)
      setTotal(arr.length)
    };
  
    const handleCancel = () => {
      setIsModalVisible(false);
    };
  
    useEffect(() => {
      axios(basicUrl+'/admin').then((res)=>{
        var first=res.data[0]
        var keys=Object.keys(first)
  
        //表头
        var column=keys.map(item=>{
          return {
            title: mapFiledToChinese[item],
            dataIndex: item,
            key:item,
          }
        })
        column.push( 
            {
            title: '操作',
            dataIndex: 'actions',
            key: 'actions',
            render:(text,record,index)=>{
                // console.log(text,record,index)
              return (
                <ButtonGroup>
                  {/* <Button type='primary' size='small' onClick={()=>{
                    console.log(record)
                }}>修改</Button> */}
                  <Button size='small' type='danger' onClick={()=>{
                      // console.log(record.id)
                      setId(record.id)
                      // console.log(id)
                      showModal()
                  }}>删除</Button>
                </ButtonGroup>
              )
            }
          })
        setColumns(column)
  
        //数据
        var list=res.data.map(item=>{
          let time =getTime(parseInt(item.time))
          return {
            key: item.id,
            id:item.id,
            name: item.name,
            tel: item.tel,
            birth: item.birth,
            position:item.position,
            address:item.address,
            time:time
          }
        })

        setData(list)
        setTotal(list.length)
        
      }).catch(err=>{
        throw err
      })
    }, [])

    return (
      <>
        <Card title="管理员列表" extra={<Button onClick={()=>{
            props.history.push('/admin/adddata')
        }}>添加数据</Button>} size="small" >
            <Table dataSource={data} columns={columns} pagination={{total:total,pageSize:5}}/>
        </Card>
        <Modal  visible={isModalVisible} onOk={()=>handleOk(id)} onCancel={handleCancel}>
            <p>您确定要删除吗</p>
        </Modal>
      </>
    )
}

export default Admin