import React from 'react'
import {Input,Button} from 'antd'

const basicUrl='https://www.prebeauty.work'

const AddData = (props) => {
    var obj={}
    var pwd1=''
    var pwd2=''
    console.log(props)
    return (
       <form style={{marginLeft:50}}>
            管理员名：
            <Input style={{width:'50%',margin:5}} placeholder="请输入管理员名" onChange={e=>{
                obj.name=e.target.value
            }}/><br/>
            联系电话：
            <Input style={{width:'50%',margin:5}} placeholder="请输入联系电话" onChange={e=>{
                obj.tel=e.target.value
            }}/><br/>
            工作内容：
            <Input style={{width:'50%',margin:5}} placeholder="请输入工作内容" onChange={e=>{
                obj.position=e.target.value
            }}/><br/>
            管理密码：
            <Input.Password placeholder="请输入密码" style={{width:'50%',margin:5}} onChange={e=>{
                pwd1=e.target.value
            }}/><br/>
            确认密码：
            <Input.Password placeholder="请确认密码" style={{width:'50%',margin:5}} onChange={e=>{
                pwd2=e.target.value
            }}/><br/>
            <Button type="primary" style={{margin:100}} onClick={()=>{
                if(pwd1===pwd2){
                    obj.password=pwd1
                    // console.log(obj)
                    fetch(basicUrl+'/admin/add',{
                        method:'post',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body:JSON.stringify(obj)
                    }).then(res=>res.json())
                    .then(res=>{
                        console.log(res);
                    })
                    alert('管理员添加成功！')
                    props.history.push('/admin/admin')

                }else{
                    alert('您的密码不一致，请重新输入')
                }
            }}>提交保存</Button>
       </form>
    )
}

export default AddData
