import { Layout, Menu,Dropdown} from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import React from 'react'
import {withRouter} from 'react-router-dom'
import {adminRouters} from '../../routers'
import {getName, removeToken} from '../../utils/auth'
import { createFromIconfontCN } from '@ant-design/icons';
import 'antd/dist/antd.css'
import './frame.css'


const { Header, Content, Sider } = Layout;
const routers=adminRouters.filter(item=>item.isTop===true)

const IconFont = createFromIconfontCN({
    scriptUrl: '//at.alicdn.com/t/font_8d5l8fzk5b87iudi.js',
});

const Frame = (props) => {
    // console.log(props)

    const adminName=getName()
    const menu = (
        <Menu onClick={(e)=>{
            // console.log(e);
            if(e.key==='setting'){
                props.history.push('/admin/my')
            }
            if(e.key==='note'){
                props.history.push('/admin/empty')
            }
            if(e.key==='exit'){
                removeToken()
                props.history.push('/login')
            }
        }}>
          <Menu.Item key='note'>通知中心</Menu.Item>
          <Menu.Item key='setting'>个人设置</Menu.Item>
          <Menu.Item key='exit'>退出</Menu.Item>
        </Menu>
    );

    return (
        <Layout>
            <Header className="header">
                <div style={{color:'white',fontSize:20}}>Info Share后台管理系统</div>
                <Dropdown overlay={menu} placement="bottomLeft" arrow>
                    <span style={{color:"white"}}>{adminName}</span>
                </Dropdown>
            </Header>
            <Layout>
            <Sider width={200} className="site-layout-background">
                <Menu
                mode="inline"
                selectedKeys={props.location.pathname}
                style={{ height: '100%', borderRight: 0 }}
                >
                    {
                        routers.map(item=>{
                            return <Menu.Item key={item.path} onClick={p=>{
                                // console.log(p.key)
                                props.history.push(p.key)
                            }}> <LaptopOutlined />{item.title}</Menu.Item>
                        })
                    }
                </Menu>
            </Sider>
            <Layout style={{ padding: '0 24px 24px' }}>
                <Content
                className="site-layout-background"
                style={{
                    padding: 24,
                    margin: 0,
                    minHeight: 280,
                }}
                >
                {props.children}
                </Content>
            </Layout>
            </Layout>
        </Layout>
    )
}

export default withRouter(Frame)
