import Login from '../views/Login'
import NotFound from '../views/NotFound'
import User from '../views/User'
import Admin from '../views/Admin'
import Activity from '../views/Activity'
import AddData from '../views/AddData'
import Confirm from '../views/Confirm'
import Confession from '../views/Confession'
import My from '../views/My'
import { Empty } from 'antd'


//应该存在两种路由
// 公共的 私有的
const adminRouters=[
    {
        path:'/admin/user',
        component:User,
        title:'用户信息',
        icon:'UserOutlined',
        isTop:true
    },
    {
        path:'/admin/admin',
        component:Admin,
        title:'管理员',
        icon:'LaptopOutlined',
        isTop:true
    },
    {
        path:'/admin/activity',
        component:Activity,
        title:'活动',
        icon:'FireOutlined',
        isTop:true
    },
    {
        path:'/admin/confession',
        component:Confession,
        title:'表白墙',
        icon:'FireOutlined',
        isTop:true
    },
    {
        path:'/admin/adddata',
        component:AddData,
        isTop:false
    },
    {
        path:'/admin/empty',
        component:Empty,
        isTop:false
    },
    {
        path:'/admin/my',
        component:My,
        isTop:false
    }
]

const commonRouters=[
    
    {
        path:'/confirm',
        component:Confirm,
    },
    {
        path:'/login',
        component:Login,
    },
    {
        path:'/404',
        component:NotFound
    },

]
export {commonRouters,adminRouters}