import React,{useState,useEffect} from 'react'
import {Card,Table,Button} from 'antd'
import ButtonGroup from 'antd/lib/button/button-group';
import axios from 'axios'

const basicUrl='https://www.prebeauty.work'

const mapFiledToChinese={
    confessionid:'序号',
    userid:'用户id',
    ccontent:'内容',
    clikesnum:'点赞数量',
    cremarksnum:'评论数量',
}

const Confession = () => {
    const [data, setData] = useState([])
    const [columns, setColumns] = useState([])
    const [total, setTotal] = useState(0)
  
    useEffect(() => {
      axios(basicUrl+'/confession').then((res)=>{
        var first=res.data[0]
        var keys=Object.keys(first)
  
        //表头
        var column=keys.map(item=>{
          return {
            title: mapFiledToChinese[item],
            dataIndex: item,
            key:item,
          }
        })
        column.push( 
            {
            title: '操作',
            dataIndex: 'actions',
            key: 'actions',
            render:(text,record,index)=>{
              return (
                <ButtonGroup>
                  <Button type='primary' size='small'>修改</Button>
                  <Button size='small'>删除</Button>
                </ButtonGroup>
              )
            }
        })
        console.log(column);
        setColumns(column)
  
        //数据
        var list=res.data.map(item=>{
          return {
            key: item.confessionid,
            confessionid:item.confessionid,
            userid:item.userid,
            ccontent:item.ccontent,
            clikesnum:item.clikesnum,
            cremarksnum: item.cremarksnum,  
          }
        })

        setData(list)
        setTotal(list.length)
        
      }).catch(err=>{
        throw err
      })
    }, [])

    return (
        <Card title="表白墙列表" size="small" >
            <Table dataSource={data} columns={columns} pagination={{total:total,pageSize:5}}/>
        </Card>
    )
}

export default Confession