import React,{useState,useEffect} from 'react'
import { getName } from '../utils/auth'

const basicUrl="https://www.prebeauty.work"
const My = () => {
    const [data, setData] = useState([])
    let name=getName()
    // console.log(name)
    useEffect(() => {
        fetch(basicUrl+'/admin/'+name).then(res=>res.json())
        .then(res=>{
            console.log(res)
            setData(res[0])
        })
    }, [])
    console.log(data)
    return (
        <div>
            姓名：<p>{data.name}</p>
            电话：<p>{data.tel}</p>
            生日：<p>{data.birth}</p>
            职位：<p>{data.position}</p>
            地址：<p>{data.address}</p>
            
        </div>
    )
}

export default My
