import React from 'react'
import {setToken,setName} from '../utils/auth'

const basicUrl='https://www.prebeauty.work'

const Login = (props) => {
    // console.log(props)
    var name=''
    var password=''
    return (
        <div style={{display:'flex',justifyContent:'center',alignItems:'center',width:'100%',height:'100%'}}>
            <div style={{display:'flex',width:'60%',height:'60%',borderColor:'#ccc',borderWidth:1,borderStyle:'solid',flexDirection:'row',borderRadius:20}}>
                <div style={{width:'50%',height:'100%',backgroundColor:'rgba(3,119,165,100)',borderTopLeftRadius:20,borderBottomLeftRadius:20,padding:50}}>
                    <p style={{fontSize:30,marginTop:50}}>Info Share</p>
                    <p style={{fontSize:20}}>大学生信息共享 后台管理系统</p>
                </div>
                <div style={{padding:50}}>
                    <p style={{fontSize:20}}>登录</p>
                    <input type='text' placeholder='用户名' onChange={(e)=>{
                        name=e.target.value
                    }} style={{borderStyle:'none',outline:'none',borderBottomWidth:1,borderBottomStyle:'solid',margin:10,width:250,padding:5}}/><br/>
                    <input type='password' placeholder='密码' onChange={(e)=>{
                        password=e.target.value
                    }} style={{borderStyle:'none',outline:'none',borderBottomWidth:1,borderBottomStyle:'solid',margin:10,width:250,padding:5}}/><br/>
                    <button style={{width:250,height:40,backgroundColor:'rgba(3,119,165,100)',borderStyle:'none',margin:10}} 
                    onClick={()=>{
                        let body={name,password}
                        fetch(basicUrl+'/login/register',{
                            method:'post',
                            headers: {
                                'Content-Type': 'application/json'
                            },
                            body:JSON.stringify(body)
                        }).then(res=>res.json())
                        .then(res=>{
                            if(res.code==='success'){
                                setToken(res.token)
                                setName(res.name)
                                props.history.push('/admin')
                            }else{
                                alert('用户名或密码错误')
                            }
                        })
                    }}>登录</button>
                </div>
            </div>
        </div>
    )
}

export default Login
