import React,{useState,useEffect} from 'react'
import {Card,Table,Button} from 'antd'
import ButtonGroup from 'antd/lib/button/button-group';
import axios from 'axios'

const basicUrl='https://www.prebeauty.work'

const mapFiledToChinese={
    id:'序号',
    name:'活动名称',
    place:'活动地点',
    time:'发起时间',
    slogan:'主题',
    holder:'举办人',
    isStart:'活动状态' 
}

const Activity = (props) => {
  // console.log(props)
    const [data, setData] = useState([])
    const [columns, setColumns] = useState([])
    const [total, setTotal] = useState(0)
  
    useEffect(() => {
      axios(basicUrl+'/activity').then((res)=>{
        var first=res.data[0]
        var keys=Object.keys(first)
  
        //表头
        var column=keys.map(item=>{
          return {
            title: mapFiledToChinese[item],
            dataIndex: item,
            key:item,
          }
        })
        column.push( 
            {
            title: '操作',
            dataIndex: 'actions',
            key: 'actions',
            render:(text,record,index)=>{
                // console.log(text,record,index)
              return (
                <ButtonGroup>
                  <Button type='primary' size='small' onClick={()=>{
                    console.log(record)
                }}>修改</Button>
                  <Button size='small' onClick={()=>{
                      console.log(record)
                  }}>删除</Button>
                </ButtonGroup>
              )
            }
          })
        setColumns(column)
  
        //数据
        let id=1
        var list=res.data.map(item=>{
          return {
            key: item.id,
            id:id++,
            name: item.name,
            place: item.place,
            time: item.time,
            slogan:item.slogan,
            holder:item.holder,
            isStart:item.isStart
          }
        })

        setData(list)
        setTotal(list.length)
        
      }).catch(err=>{
        throw err
      })
    }, [])

    return (
        <Card title="活动列表" extra={<Button onClick={()=>{
            // props.history.push('/admin/adddata')
        }}>添加数据</Button>} size="small" >
            <Table dataSource={data} columns={columns} pagination={{total:total,pageSize:5}}/>
        </Card>
    )
}

export default Activity
