import React,{useState,useEffect} from 'react'
import {Card,Table,Button} from 'antd'
import ButtonGroup from 'antd/lib/button/button-group';
import axios from 'axios'

const basicUrl='https://www.prebeauty.work'

const mapFiledToChinese={
    id:'序号',
    name:'姓名',
    tel:'电话',
    birth:'生日',
    email:'邮箱',
    address:'地址' 
}

const User = () => {
    const [data, setData] = useState([])
    const [columns, setColumns] = useState([])
    const [total, setTotal] = useState(0)
  
    useEffect(() => {
      axios(basicUrl+'/user').then((res)=>{
        var first=res.data[0]
        var keys=Object.keys(first)
  
        //表头
        var column=keys.map(item=>{
          return {
            title: mapFiledToChinese[item],
            dataIndex: item,
            key:item,
          }
        })
        column.push( 
            {
            title: '操作',
            dataIndex: 'actions',
            key: 'actions',
            render:(text,record,index)=>{
              return (
                <ButtonGroup>
                  {/* <Button type='primary' size='small'>修改</Button> */}
                  <Button size='small' type="danger">禁用</Button>
                </ButtonGroup>
              )
            }
          })
        setColumns(column)
  
        //数据
        var list=res.data.map(item=>{
          return {
            key: item.id,
            id:item.id,
            name: item.name,
            tel: item.tel,
            birth: item.birth,
            email:item.email,
            address:item.address
          }
        })

        setData(list)
        setTotal(list.length)
        
      }).catch(err=>{
        throw err
      })
    }, [])

    return (
        <Card title="用户列表" size="small" >
            <Table dataSource={data} columns={columns} pagination={{total:total,pageSize:5}}/>
        </Card>
    )
}

export default User